                  <v-avatar icon small size="40px" class="ml-3">
                    <v-icon size="22px" color="black">mdi-eye-outline</v-icon>
                  </v-avatar>
                  <span>{{ item.visit_num }}</span>
                  <v-avatar icon small size="40px" class="ml-3">
                    <v-icon size="22px" color="black">mdi-comment-processing-outline</v-icon>
                  </v-avatar>
                  <span>{{ item.comment_num }}</span>
