              </v-card-text>
            </v-card>
          </div>
        </div>

        <!-- 精选 -->
        <div v-else>
          <div v-for="(item, i) in digestPosts" :key="i" class="d-flex">
            <v-card
              class="mb-3 mx-auto rounded-lg"
