                  <v-icon size="30px">mdi-dots-vertical</v-icon>
                </v-btn>
              </v-card-title>
              <v-card-text class="mt-1 d-flex flex-column pb-0">
                <span class="text_small text_thick text_black">{{ item.title }}</span>
                <span class="text_small--- line_three">{{ item.content }}</span>
                <span>
                  <v-chip class="ma-1" color="primary" outlined small>
                    <v-icon left size="20px">{{ map.categoryToAvatar[item.category] }}</v-icon>
                    <span>{{ item.category }}</span>
                  </v-chip>
                </span>
                <span class="d-flex align-center">
                  <v-btn icon small @click.stop="likeOrDislike(i, 1, 'digestPosts')">
                    <v-icon size="22px" color="black">mdi-thumb-up-outline</v-icon>
                  </v-btn>
                  <span class="ml-1">{{ item.like_num }}</span>

                  <v-avatar icon size="40px" class="ml-5">
                    <v-icon size="22px" color="black">mdi-eye-outline</v-icon>
                  </v-avatar>
                  <span>{{ item.visit_num }}</span>
                  <v-avatar icon size="40px" class="ml-6">
                    <v-icon size="22px" color="black">mdi-comment-processing-outline</v-icon>
                  </v-avatar>
                  <span>{{ item.comment_num }}</span>
                </span>
              </v-card-text>
            </v-card>

            <!-- 需要折叠的部分 -->
            <v-card
              class="mb-3 mx-auto rounded-lg"
              width="100%"
              flat
              rounded
              v-else
              @click="
                $router.push({
                  path: '/postDetail',
                  query: item
                });
                $store.change_scrollYLeave();
              "
            >
              <v-card-title class="pt-2 pb-0 d-flex">
                <v-avatar size="45px">
                  <img :src="item.avatar" alt="John" />
                </v-avatar>
                <span class="d-flex flex-column justify-center ml-2">
                  <div class="d-flex">
                    <span class="text_small text_thick">#{{ item.thread_starter_name }}</span>
                    <v-chip
                      outlined
                      x-small
                      color="primary"
                      class="mt-2 ml-2 px-2 d-flex align-center"
                      v-if="item.vote != null"
                    >
                      <span class>投票</span>
                    </v-chip>
                    <v-chip
                      outlined
                      x-small
                      color="primary"
                      class="mt-2 ml-2 px-2 d-flex align-center"
                      v-if="item.digest != null"
                    >
                      <span class>加精</span>
                    </v-chip>
                  </div>
                  <span
                    class="text_small-- text_grey mt-n2"
                  >{{ timeFormat(item.latest_comment_time) }}</span>
                </span>
                <v-spacer></v-spacer>
                <v-btn icon @click.stop="showBottomDrawer(i, 'digestPosts')">
                  <v-icon size="30px">mdi-dots-vertical</v-icon>
                </v-btn>
              </v-card-title>
              <v-card-text class="pb-0">
                <div class="d-flex flex-column">
                  <div class="d-flex align-center">
                    <div
                      class="text-decoration-line-through text_small text_thick black--text"
                    >【已折叠 】</div>
                    <v-btn fab x-small text class="pa-0">
                      <v-icon
                        size="20px"
                        color="black"
                        @click.stop="openWhyFoldedDialog = true"
                      >mdi-help-circle</v-icon>
