        </v-card-title>
        <v-divider></v-divider>
        <v-card-text class="mt-2">
          <div>
            <span>『火锅底料』社区热度计算公式:</span>
            <br />
            <span>
              热度 = 浏览量 × 1 + 评论数 × 2 + 点赞数 × 3 + 收藏数 × 4 - 举报数
              × 5
            </span>
            <br />
            <span>热度排名由上往下取前十</span>
            <span></span>
          </div>
