</template>

<script>
import data from "@/data.js";
import map from "@/map/map.js";
import MescrollVue from "mescroll.js/mescroll.vue";

export default {
  name: "hot",
  components: {
    MescrollVue // 注册mescroll组件
  },
  data() {
    return {
      tab: 0,
      tabs: ["热门", "精选"],
      map: map,
      HotPostRecently: [],
      digestPosts: [],
      snackbar: {
        show: false,
        message: "再点一次退出",
        color: "grey-darken3"
      },
      bottomDrawer: {
        showBottomDrawer: false,
        chooseIndex: 0,
        dislike_num: 0,
        listName: "HotPostRecently"
      },
      hotExplainDialog: false,
      down1: {
        use: false
      },
      up1: {
        onScroll: this.onScroll,
        use: false
      },
      down2: {
        use: false
      },
      up2: {
        onScroll: this.onScroll,
        callback: (page, mescroll) => {
          mescroll.endSuccess(0);
        }
      }
    };
  },
  created() {
    console.log(location.hash);
    let that = this;
    that
      .$request("get", "/forum/getHotPostRecently", {}, {})
      .then(res => {
        that.HotPostRecently = res.data;
      })
      .catch(e => {
        that.showSnackbar(e);
      });
    setTimeout(() => {
      that
        .$request("get", "/forum/getDigestPosts", {}, {})
        .then(res => {
          that.digestPosts = res.data;
        })
        .catch(e => {
          that.showSnackbar(e);
        });
    }, 500);
  },
  methods: {
    onScroll(mescroll, y, isUp) {
      this.$store.change_isUp(isUp);
    },
    //计算时间差
    timeFormat(latest_comment_time) {
      //一共这么多秒前
      let t = Math.floor(
        (new Date().getTime() - new Date(latest_comment_time).getTime()) / 1000
      );
      const d = Math.floor(t / 3600 / 24);
      const h = Math.floor(t / 3600) % 24;
      const m = Math.floor(t / 60) % 60;
      const s = Math.floor(t % 60);
      let res = "";
      if (s > 0 && m == 0 && h == 0 && d == 0) {
        res = s + "秒前";
      } else if (m > 0 && h == 0 && d == 0) {
        res = m + "分钟前";
      } else if (h > 0 && d == 0) {
        res = h + "小时前";
      } else {
        let date = new Date(
          new Date(latest_comment_time).getTime() + 8 * 3600 * 1000
        );
        return date
          .toJSON()
          .substr(0, 16)
          .replace("T", " ");
      }
      return res;
    },
    likeOrDislike(i, value, listName) {
      let that = this;
      that
        .$request(
          "post",
          "/forum/forumLikeOrDislike",
          {},
          {
            postId: Number(that[listName][i].id),
            value
          }
        )
        .then(res => {
          that.showSnackbar(res, "ok");
          if (value == 1) that[listName][i].like_num++;
          else if (value == -1) {
            that.bottomDrawer.dislike_num++;
            that[listName][i].dislike_num++;
          }
        })
        .catch(e => {
          console.log(e);
          that.showSnackbar(e);
        });
    },
    addCollect(i, listName) {
      let that = this;
      that
        .$request(
          "post",
          "/forum/addCollect",
          {},
          {
            postId: Number(that[listName][i].id)
          }
        )
        .then(res => {
          that.showSnackbar(res, "ok");
        })
        .catch(e => {
          that.showSnackbar(e);
        });
    },
    showBottomDrawer(i, listName) {
      this.bottomDrawer.showBottomDrawer = true;
      this.bottomDrawer.chooseIndex = i;
      this.bottomDrawer.dislike_num = this[listName][i].dislike_num;
      this.bottomDrawer.listName = listName;
    }
  },
  watch: {
    hotExplainDialog(v) {
      this.$store.change_dialog(v, "hotExplainDialog");
    },
    "$store.state.dialog"(v) {
      if (this.$store.state.dialogName == "hotExplainDialog")
        this.hotExplainDialog = v;
    }
  }
};
</script>

<style>
.main_container {
  width: 100%;
  /* margin-top:env(safe-area-inset-top); */
  margin-top: calc(60px+env(safe-area-inset-top));
}

.mescroll_container_hot {
  position: fixed;
  top: 110px;
  bottom: 0;
  height: auto; /*如设置bottom:50px,则需height:auto才能生效*/
  width: 100%;
  background-color: #f5f5f5;
}
</style>



